import React, {Component} from 'react'
import { Link } from 'react-router-dom'
import SignInLinks from './SignInLinks'
import SignOutLinks from './SignOutLinks'
import {connect} from 'react-redux'
import {AuthIsLoaded} from '../../hoc/auth/authIsLoaded'
import { render } from '@testing-library/react'

class Navbar extends Component {

    state = {
        isMenuHidden: true
    }

    handleClick = (e) => {
        const hiddenMenu = document.querySelector('.hidden-menu');
        
        if (this.state.isMenuHidden) {
            hiddenMenu.style.display = 'block';
            
            this.setState({
                ...this.state,
                isMenuHidden:false
            });
        } else {
            hiddenMenu.style.display = 'none'

            this.setState({
                ...this.state,
                isMenuHidden:true
            });
        }

    }

    render() {
        const {auth}= this.props;

        const links = auth.uid ? <SignInLinks/> : <SignOutLinks/>;

        return (
            <div>
                <nav className="nav-wrapper grey darken-4 z-depth-1">
    
                    <div className="container">
                        <Link to='/' className="brand-logo left">
                            <i className="fas fa-pen-square blue-text text-lighten-1"></i>
                            <span className="white-text">My Blog</span>
                        </Link>
                        
                        <AuthIsLoaded >
                            <div className='hide-on-small-only'>
                                {links}
                            </div>
                        </AuthIsLoaded>
    
                        <Link className='hide-on-med-and-up' to='/'> 
                            <i className="large material-icons right " onClick={(e)=> this.handleClick(e)}>menu</i>
                        </Link>
        
                    </div>
    
                </nav>
    
                <nav style={{display:'none'}} className='hidden-menu nav-wrapper grey darken-4 z-depth-1'>
                    <div className='container'>
                        {links}
                    </div>
                </nav>
    
            </div>
        )
    }

}

const mapStateToProps = (state) => {
    return {
        auth: state.firebase.auth
    }
}

export default connect(mapStateToProps)(Navbar)
